import { AbstractCell } from './abstract.cell';
import { cell } from './register';
import { TextCellProps } from '../../../types';

@cell('text')
export class TextCell extends AbstractCell {
    protected text: string;
    protected textAlign: string;

    public constructor(props: TextCellProps) {
        super(props);
        this.text = props.text;
    }

    protected _copyHtml(): string {
        return '';
    }

    protected _copyText(): string {
        return '';
    }
}
